.Home{display:flex;flex-direction:row}.Home .column{flex-basis:1;width:50%;height:100%;padding:24px}.Home .previewImageWrap,.Home .renderImageWrap{width:100%;padding:24px;background:#e9e9e9;overflow:hidden}.Home .newImg{overflow:scroll}.Home .formItem{border-bottom:1px dashed #ddd;padding:8px 0;position:relative}.Home .formItem button{position:absolute;right:0;top:8px}.Home .formItem.isActive{background:rgba(0,0,0,0.03)}.Home .formItem.isActive h5 span{color:lime}.Home .formItem.isActive button{background:lime}.Home .imageCard{width:150px;height:200px;background:#f9f9f9;box-shadow:0 1px 4px rgba(0,0,0,0.1);display:inline-block;margin:0 16px;font-size:11px;position:relative}.Home .imageCard:hover{background:#fff;box-shadow:0 1px 4px rgba(0,0,0,0.2)}.Home .imageCard button{position:absolute;bottom:-10px;left:50%}



