.Edit{display:flex;flex-direction:row}.Edit .column{flex-basis:1;width:50%;height:100%;padding:24px}.Edit .previewImageWrap,.Edit .renderImageWrap,.Edit .sourceImageWrap{width:100%;padding:24px;background:#e9e9e9;overflow:hidden}.Edit .previewImageWrap{padding:0;background:none}.Edit .renderImageWrap{visibility:hidden;height:1px;overflow:hidden}.Edit .renderImageWrap.visible{visibility:visible;height:inherit;overflow:hidden}.Edit .sourceImageWrap{padding:0;height:1px;overflow:hidden}.Edit .sourceImageWrap.visible{visibility:visible;height:inherit;overflow:hidden}.Edit .titleBlock{position:relative;border-top:1px dotted #777;margin-top:4px}.Edit .titleBlock button{position:absolute;right:0;top:4px}.Edit .newImg{overflow:scroll}.Edit .renderImage{width:20%;display:inline-block;vertical-align:top;position:relative}.Edit .renderImage>div{width:100%;padding:8px}.Edit .renderImage>div img{width:100%}.Edit .renderImage h4{color:#ccc;margin:0}.Edit .renderImage .placeholder{min-height:170px;margin-top:4px;background:#f3f3f3;width:auto;margin-left:4px;margin-right:4px;font-size:11px;font-style:italic;text-align:center;line-height:150px;color:#777}.Edit .renderImage .placeholder+div{height:1px;overflow:hidden}.Edit .renderImage .theTick{width:40px;height:40px;position:absolute;top:-8px;right:8px}.Edit .renderImage .theTick svg{position:absolute;left:0;top:0;width:100%;height:100%}.Edit .renderImage .theTick svg g path{fill:rgba(0,0,0,0.1)}.Edit .renderImage.done h4{color:#222}.Edit .renderImage.done .theTick svg g path{fill:#AFD621}.Edit .saving{font-style:italic;background:#f3f3f3;width:auto;margin-left:4px;margin-right:4px;font-size:11px;font-style:italic;text-align:center;color:#777}.Edit .theLoading{width:20px !important;height:20px;position:relative;display:inline-block;line-height:2em;vertical-align:middle;margin-right:6px;animation-duration:1s;animation-name:rotateMe;animation-timing-function:linear;animation-iteration-count:infinite}.Edit .theLoading svg{position:absolute;left:0;top:0;width:100%;height:100%}.Edit .theLoading svg g path{fill:rgba(0,0,0,0.5)}.Edit .formItem{border-bottom:1px dashed #ddd;padding:8px 0;position:relative}.Edit .formItem button{position:absolute;right:0;top:8px}.Edit .formItem.isActive{background:rgba(0,0,0,0.03)}.Edit .formItem.isActive h5 span{color:lime}.Edit .formItem.isActive button{background:lime}.Edit section.alt{background:#f9f9f9;padding:16px}@keyframes rotateMe{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}



