.ImageAdmin{position:relative}.ImageAdmin .notice{position:absolute;right:16px;top:16px;min-width:200px;min-height:50px;visibility:hidden;opacity:0;transition:opacity .3s ease;line-height:2em;padding:8px;color:#627323}.ImageAdmin.isUpdated .notice{visibility:visible;opacity:1;border:1px solid #AFD621;background:#f9f9f9;transition:opacity .3s ease}.ImageAdmin.isUpdated .theTick{display:block}.ImageAdmin.isUpdated .theTick svg g path{fill:#AFD621}.ImageAdmin .theTick{vertical-align:middle;display:none;width:40px;height:40px;position:absolute;top:-4px;left:-8px}.ImageAdmin .theTick svg{position:absolute;left:0;top:0;width:100%;height:100%}.ImageAdmin .theTick svg g path{fill:rgba(0,0,0,0.1)}



